import React from "react";

// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  Row,
  Col,
  Button,
  FormGroup,
  Input,
  Label
} from "reactstrap";

import "./../assets/css/custom.css";

import axios from "axios";
import { Redirect } from "react-router-dom";
import { ClipLoader } from "react-spinners";
import settings from "./settins";
class Icons extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      regions: {},
      redirect: false
    };

    this.taskName = React.createRef();
    this.provider_ = undefined;
    this.quantity = React.createRef();
    this.authUsername = React.createRef();
    this.authPassword = React.createRef();
    this.location_ = undefined;

    this.endpoint = settings.endpoint;
  }

  setLocation = e => {
    this.location = e.target.value;
  };

  getLocation = e => {
    this.setState({
      loading: true
    });

    this.provider = e.target.value;

    axios.get(this.endpoint + "/regions/" + e.target.value).then(({ data }) => {
      this.setState({
        regions: data,
        loading: false
      });
    });
  };

  createTask = () => {
    //Client side validate data here.
    var payload = {
      taskName: this.taskName.current.value,
      provider: this.provider,
      quantity: this.quantity.current.value,
      authUserName: this.authUsername.current.value,
      authPassword: this.authPassword.current.value,
      location: this.location
    };
    axios.post(this.endpoint + "/tasks/create", payload).then(({ data }) => {
      if (data.success) {
        window.location = this.endpoint + "/suite/proxies";
      }
    });
  };

  renderLocation = () => {
    var locations = Object.keys(this.state.regions);
    var rendered = [];

    locations.forEach(location => {
      var current = this.state.regions[location];
      rendered.push(
        <option value={location}>
          {" "}
          {current.country} - {current.name}{" "}
        </option>
      );
    });

    return rendered;
  };

  render() {
    return (
      <div className="content">
        <Row>
          <Col md="12">
            <Card>
              <CardHeader>
                <h5 className="title">Create Proxies</h5>
                <h4>Create a Proxy Creation Task</h4>

                <h4>
                  {" "}
                  <strong>
                    {" "}
                    <i
                      className="tim-icons icon-app"
                      style={{ padding: "10px" }}
                    />{" "}
                    STEP 1{" "}
                  </strong>
                  <h5>
                    Pick providers. You can only select providers you have added
                    keys for in your profile settings.
                  </h5>
                  <h5>
                    <b>Note :</b> Beta currently only supports one provider per
                    task.
                  </h5>
                </h4>
              </CardHeader>
              <CardBody className="all-icons">
                <FormGroup>
                  <Label for="exampleSelectMulti">Select Provider</Label>
                  <Input
                    type="select"
                    name="selectMulti"
                    id="exampleSelectMulti"
                    multiple
                    onChange={this.getLocation}
                  >
                    <option value="vultr">Vultr</option>
                    <option value="linode">Linode</option>
                    <option value="digio">Digital Ocean</option>
                    <option value="google">Google Cloud</option>
                    <option value="aws">Amazon Web Services</option>
                  </Input>
                </FormGroup>
                <br />
                <h4>
                  {" "}
                  <strong>
                    {" "}
                    <i
                      className="tim-icons icon-istanbul"
                      style={{ padding: "10px" }}
                    />{" "}
                    STEP 2{" "}
                  </strong>
                  <br />
                  <h5>
                    Pick A Location. Your proxy servers will be deployed in the
                    region you select.
                    <div className={this.state.loading ? "visible" : "hide"}>
                      Fetching in stock locations from selected provider
                      {"    "}
                      <ClipLoader
                        sizeUnit={"px"}
                        size={15}
                        color={"#e14eca"}
                        loading={this.state.loading}
                      />{" "}
                    </div>
                  </h5>
                </h4>
                <FormGroup>
                  <Label for="exampleSelectMulti">Select Location</Label>
                  <Input
                    type="select"
                    name="selectMulti"
                    id="exampleSelectMulti"
                    multiple
                    onChange={this.setLocation}
                  >
                    <this.renderLocation />
                  </Input>
                </FormGroup>
                <h4>
                  {" "}
                  <strong>
                    {" "}
                    <i
                      className="tim-icons icon-settings"
                      style={{ padding: "10px" }}
                    />{" "}
                    STEP 3{" "}
                  </strong>
                  <br />
                  <h5>Pick the quantity and configure authentication.</h5>
                </h4>
                <Row>
                  <Col className="pr-md-1" md="4">
                    <FormGroup>
                      <label>Task Name</label>
                      <Input
                        placeholder="Task Name"
                        type="text"
                        innerRef={this.taskName}
                      />
                    </FormGroup>
                  </Col>
                  <Col className="px-md-1" md="4">
                    <FormGroup>
                      <label>Proxy Quantity</label>
                      <Input
                        placeholder="Quantity"
                        type="text"
                        innerRef={this.quantity}
                      />
                    </FormGroup>
                  </Col>
                </Row>
                <br />
                <Row>
                  <Col className="pr-md-1" md="4">
                    <FormGroup>
                      <label>Proxy Username</label>
                      <Input
                        placeholder="Username"
                        innerRef={this.authUsername}
                        type="text"
                      />
                    </FormGroup>
                  </Col>
                  <Col className="px-md-1" md="4">
                    <FormGroup>
                      <label>Proxy Password</label>
                      <Input
                        innerRef={this.authPassword}
                        placeholder="Password"
                        type="text"
                      />
                    </FormGroup>
                  </Col>
                </Row>
                <Button
                  className="btn-fill"
                  color="primary"
                  type="submit"
                  onClick={this.createTask}
                >
                  Start Task
                </Button>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Icons;
