import React from "react";
// react plugin for creating notifications over the dashboard
import NotificationAlert from "react-notification-alert";
import axios from "axios";

// reactstrap components
import {
  Alert,
  UncontrolledAlert,
  Button,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Row,
  Col,
  UncontrolledTooltip,
  DropdownMenu,
  DropdownToggle,
  FormGroup,
  Table,
  Label,
  Input,
  DropdownItem,
  UncontrolledDropdown,
  Progress
} from "reactstrap";
import { throws } from "assert";
import settings from "./settins";
class Proxies extends React.Component {
  constructor(props) {
    super(props);

    this.tasks = [];
    this.state = {
      tasks: [],
      taskMap: {},
      proxies: [],
      actions: [{}]
    };

    this.endpoint = settings.endpoint;
  }

  deleteSelected = () => {
    axios.post(this.endpoint + "/tasks/delete", {
      taskMap: this.state.taskMap
    });
  };
  pollTasks = () => {
    axios.get(this.endpoint + "/tasks/all").then(({ data }) => {
      this.tasks = data;
      this.setState({
        tasks: data
      });
    });
  };
  componentDidMount() {
    axios.get(this.endpoint + "/actions/all").then(({ data }) => {
      this.setState({
        actions: data
      });
    });
    this.pollTasks();
    setInterval(this.pollTasks, 500);
  }

  taskChecked = taskId => {
    if (this.state.taskMap[taskId]) {
      var newTaskMap = this.state.taskMap;
      delete newTaskMap[taskId];
      console.log(newTaskMap);
      this.setState({
        taskMap: newTaskMap
      });
    } else {
      axios.get(this.endpoint + "/tasks/json/" + taskId).then(({ data }) => {
        var newTaskMap = this.state.taskMap;
        newTaskMap[taskId] = data;
        console.log(newTaskMap);
        this.setState({
          taskMap: newTaskMap
        });
      });
    }
  };

  taskData = props => {
    if (props.task.failed) {
      return "Failed - Rejected(check your quota)";
    } else {
      return props.task.success ? "Completed" : "Pending";
    }
  };

  //Task Table production
  produceTaskTable = () => {
    var taskTable = [];
    var index = 0;
    this.state.tasks.forEach(task => {
      taskTable.push(
        <tr key={index}>
          <td>
            <FormGroup check>
              <Label check>
                <Input
                  onChange={() => {
                    this.taskChecked(task._id);
                  }}
                  defaultValue=""
                  type="checkbox"
                />
                <span className="form-check-sign">
                  <span className="check" />
                </span>
              </Label>
            </FormGroup>
          </td>
          <td>
            <p className="title">{task.taskName}</p>
            <p className="text-muted">
              {task.provider}, {task.location} - {task.quantity} Proxies (
              <this.taskData task={task} />)
              <Progress
                value={(task.progressQuantity / task.quantity) * 100}
                style={{
                  marginTop: "6px"
                }}
              />
            </p>
          </td>
          <td className="td-actions text-right">
            <Button
              color="link"
              id="tooltip636901683"
              title=""
              onClick={() => {
                this.downloadProxies(task._id);
              }}
              type="button"
            >
              <i className="tim-icons icon-notes" />
            </Button>
            <UncontrolledTooltip
              delay={0}
              target="tooltip636901683"
              placement="right"
            >
              Download Proxies
            </UncontrolledTooltip>
          </td>
        </tr>
      );
      index++;
    });

    return taskTable;
  };

  resolveActionHeader = code => {
    switch (code) {
      case 1:
        return "Success";
      case 2:
        return "Info";
      case 3:
        return "Failed";
      default:
        return "Server Error";
    }
  };

  produceActionsTable = () => {
    var actions = [];
    var index = 0;
    this.state.actions.forEach(action => {
      actions.push(
        <UncontrolledAlert
          key={index}
          color={this.resolveCode(action.actionType)}
        >
          <span>
            <b>{this.resolveActionHeader(action.actionType)} - </b>
            {action.actionMessage}
          </span>
        </UncontrolledAlert>
      );
      index++;
    });

    return actions;
  };

  resolveCode = code => {
    switch (code) {
      case 1:
        return "success";
      case 2:
        return "info";
      case 3:
        return "danger";
      default:
        return "info";
    }
  };

  downloadProxies = id => {
    window.open(this.endpoint + "/tasks/" + id);
  };
  produceProxyTable = () => {
    var flatened = [];

    Object.keys(this.state.taskMap).forEach(taskey => {
      this.state.taskMap[taskey].forEach(proxy => {
        flatened.push(proxy);
      });
    });

    var proxyTable = [];
    var index = 0;
    flatened.forEach(proxy => {
      proxyTable.push(
        <tr key={index}>
          <td>{proxy.proxyString}</td>
          <td>{proxy.location}</td>
          <td>{proxy.provider}</td>
          <td>{proxy.taskName}</td>
        </tr>
      );
      index++;
    });
    return proxyTable;
  };

  notify = place => {
    var color = Math.floor(Math.random() * 5 + 1);
    var type;
    switch (color) {
      case 1:
        type = "primary";
        break;
      case 2:
        type = "success";
        break;
      case 3:
        type = "danger";
        break;
      case 4:
        type = "warning";
        break;
      case 5:
        type = "info";
        break;
      default:
        break;
    }
    var options = {};
    options = {
      place: place,
      message: (
        <div>
          <div />
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    this.refs.notificationAlert.notificationAlert(options);

    this.endpoint = "http://140.82.4.135/";
  };
  render() {
    return (
      <>
        <div className="content">
          <div className="react-notification-alert-container">
            <NotificationAlert ref="notificationAlert" />
          </div>
          <Row>
            <Col md="6">
              <Card className="card-tasks">
                <CardHeader>
                  <h6 className="title d-inline">
                    Tasks({this.state.tasks.length})
                  </h6>
                  <p className="card-category d-inline"> Recent</p>
                  <UncontrolledDropdown>
                    <DropdownToggle
                      caret
                      className="btn-icon"
                      color="link"
                      data-toggle="dropdown"
                      type="button"
                    >
                      <i className="tim-icons icon-settings-gear-63" />
                    </DropdownToggle>
                    <DropdownMenu aria-labelledby="dropdownMenuLink" right>
                      <DropdownItem
                        href="#pablo"
                        onClick={e => {
                          e.preventDefault();
                          this.deleteSelected();
                        }}
                      >
                        Delete Proxies
                      </DropdownItem>
                    </DropdownMenu>
                  </UncontrolledDropdown>
                </CardHeader>
                <CardBody>
                  <div className="table-full-width table-responsive">
                    <Table>
                      <tbody>
                        <this.produceTaskTable />
                      </tbody>
                    </Table>
                  </div>
                </CardBody>
              </Card>
            </Col>
            <Col md="6">
              <Card>
                <CardHeader>
                  <CardTitle tag="h4">Task Status</CardTitle>
                </CardHeader>
                <CardBody>
                  <this.produceActionsTable />
                </CardBody>
              </Card>
            </Col>
            <Col md="12">
              <FormGroup>
                <label>Select a task to see it's created proxies.</label>
                <Input
                  placeholder="Search a proxy with it's IP / PROVIDER / LOCATION"
                  type="text"
                />
              </FormGroup>
              <Card>
                <CardBody>
                  <Table className="tablesorter" responsive>
                    <thead className="text-primary">
                      <tr>
                        <th>Proxy</th>
                        <th>Location</th>
                        <th>Provider</th>
                        <th className="text-center">Task Name</th>
                      </tr>
                    </thead>
                    <tbody>
                      <this.produceProxyTable />
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </>
    );
  }
}

export default Proxies;
