import React from "react";
import axios from "axios";
// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardText,
  FormGroup,
  Form,
  Input,
  Row,
  Col
} from "reactstrap";
import settings from "./settins";
class UserProfile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      _id: "",
      linodeKey: "",
      vultrKey: "",
      digitalOceanKey: "",
      awsKey: "",
      pageStatus: "",
      status: ""
    };
    this.linodeKey = React.createRef();
    this.digitalOceanKey = React.createRef();
    this.vultrKey = React.createRef();
    this.awsKey = React.createRef();
    this.googleKey = React.createRef();

    this.endpoint = settings.endpoint;
  }

  componentDidMount() {
    axios.get(this.endpoint + "/profile/display").then(response => {
      var data = response.data;
      this.setState(data[0]);
    });
  }

  postFormData = () => {
    //Posts form data

    var linodeKey = this.linodeKey.current.value;
    var digitalOceanKey = this.digitalOceanKey.current.value;
    var vultrKey = this.vultrKey.current.value;
    var awsKey = this.awsKey.current.value;
    var googleKey = this.googleKey.current.value;

    axios
      .post(this.endpoint + "/profile/update", {
        linodeKey,
        digitalOceanKey,
        vultrKey,
        awsKey,
        googleKey
      })
      .then(({ data }) => {
        if (data.error) {
          this.setState({
            status: "An error occured while updating your profile!"
          });
        } else {
          this.setState({
            status: "Your profile data was sucessfully updated!"
          });
        }
      });
  };

  render() {
    return (
      <>
        <div className="content">
          <Row>
            <Col md="8">
              <Card>
                <CardHeader>
                  <h5 className="title">Edit Profile</h5>
                  <h5>{this.state.status}</h5>
                </CardHeader>
                <CardBody>
                  <Form>
                    <Row>
                      <Col className="pr-md-1" md="6">
                        <FormGroup>
                          <label>Username</label>
                          <Input
                            defaultValue={this.state.username}
                            ref={this.username}
                            placeholder="Company"
                            type="text"
                            disabled
                          />
                        </FormGroup>
                      </Col>
                      <Col className="pl-md-1" md="6">
                        <FormGroup>
                          <label>Key</label>
                          <Input
                            defaultValue={this.state._id}
                            placeholder="Last Name"
                            type="text"
                            disabled
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="12">
                        <FormGroup>
                          <label>Linode API Key</label>
                          <Input
                            defaultValue={this.state.linodeKey}
                            innerRef={this.linodeKey}
                            placeholder="Enter key to start making Linode Proxies"
                            type="text"
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="12">
                        <FormGroup>
                          <label>Vultr API Key</label>
                          <Input
                            defaultValue={this.state.vultrKey}
                            innerRef={this.vultrKey}
                            placeholder="Enter key to start making Vultr Proxies"
                            type="text"
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="12">
                        <FormGroup>
                          <label>Digital Ocean API Key</label>
                          <Input
                            defaultValue={this.state.digitalOceanKey}
                            innerRef={this.digitalOceanKey}
                            placeholder="Enter key to start making Digital Ocean Proxies"
                            type="text"
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="12">
                        <FormGroup>
                          <label>
                            AWS API Key in the following format (Acess Key
                            Id;Acess Key Secrect) format <br />
                            Please subscribe and agree to this amazon Image on
                            the account you are using before:
                            <a href="https://aws.amazon.com/marketplace/pp?sku=aw0evgkw8e5c1q413zgy5pjce">
                              {" "}
                              agree here{" "}
                            </a>
                          </label>
                          <Input
                            defaultValue={this.state.awsKey}
                            innerRef={this.awsKey}
                            placeholder="Enter key to start making Digital Ocean Proxies"
                            type="text"
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="12">
                        <FormGroup>
                          <label>
                            Google API Key in the following format (project
                            name;account email; private key)
                          </label>
                          <Input
                            defaultValue={this.state.googleKey}
                            innerRef={this.googleKey}
                            placeholder="Enter key to start making Digital Ocean Proxies"
                            type="text"
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                  </Form>
                </CardBody>
                <CardFooter>
                  <Button
                    className="btn-fill"
                    color="primary"
                    type="submit"
                    onClick={this.postFormData}
                  >
                    Save
                  </Button>
                </CardFooter>
              </Card>
            </Col>
            <Col md="4">
              <Card className="card-user">
                <CardBody>
                  <CardText />
                  <div className="author">
                    <div className="block block-one" />
                    <div className="block block-two" />
                    <div className="block block-three" />
                    <div className="block block-four" />
                    <a href="#pablo" onClick={e => e.preventDefault()}>
                      <img
                        alt="..."
                        className="avatar"
                        src={require("assets/img/emilyz.jpg")}
                      />
                      <h5 className="title">{this.state.username}</h5>
                    </a>
                    <p className="description">Premium Account</p>
                  </div>
                  <div className="card-description" />
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </>
    );
  }
}

export default UserProfile;
